.stars {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;

  &__list {
    display: flex;
  }

  &__star {
    width: 16px;
    height: 16px;
    background-image: url(../images/star.svg);
    margin-right: 4px;
    background-position: center;
    background-repeat: no-repeat;

    &:last-child {
      margin-right: 0;
    }

    &:nth-child(-n + 4) {
      background-image: url(../images/star-active.svg);
    }
  }

  &__reviews {
    font-size: 10px;
    line-height: 14px;
  }
}
